import React from "react";
import {MDBContainer} from "mdb-react-ui-kit";

function Offer(){
    return <MDBContainer breakpoint="lg"><h1 className="about_h1">Edzseim</h1>
        <div className="container">
            <div className="row">
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/victor-freitas-WvDYdXDzkhs-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Egyni alkalmak</h2>
                                <p>Egy llapotfelmrst kveten szemlyre szabott edzsterv szerint haladunk elre az alakod formlsban. Megmutatom neked azt, hogy milyen eredmnyes s hasznos lehet egy ra aktv mozgs.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/meghan-holmes-buWcS7G1_28-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Pros edzs</h2>
                                <p>Nem szeretsz egyedl edzeni? Gyertek prban, gy kzsen motivlhatjtok egymst a feladatok sorn! Az eredmny megosztott figyelem mellett is garantlt - senkinl sem marad szrazon a trlkz.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/kelly-sikkema-IZOAOjvwhaM-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Brlet</h2>
                                <p>Hossztvon jrnl hozzm?
                                    10 alkalmas brletet vlthatsz nlam, amellyel idt s pnzt sprohatsz meg. A megfelel edzsterv a rendszeressg
                                    alapjain nyugszik - ez pedig
                                    egy j letmd kialaktsnak kulcsa. </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div></MDBContainer>
}

export default Offer;
