import React from "react";
import {MDBContainer} from "mdb-react-ui-kit";

function About(){
    return <MDBContainer breakpoint="lg">
        <div className={"aboutSquare"}><div className={"aboutSquareInner"}><p className="about_p">Szia! <br/><br/>
            <a href="">Betti vagyok</a> s alakaformlssal foglalkozom,
            azaz <a href="">szemlyi edz</a> vagyok! <br/><br/>
            Nem tlagos mdszerekkel dolgozom,
            a legvltozatosabb feladatokat ptem bele az edzstervekbe, amelyet <a href="">szemlyre szabottan</a> ksztek el minden vendgemnek. <br/><br/>
            Egytt dolgozva nem csak egy j alakot, hanem
            egy <a href="">egszsges letmdot</a> is kialaktunk! <br/><br/>
            Megmutatom neked, hogy a <a href=""> mozgs szenvedly</a>,
            s ez a lelkeseds nem csak egy nyri
            beach body erejig tart.</p>
            <button className="btn fitButton">Tudj meg rlam tbbet!</button>
        </div>
        </div>
        <div className="float-end about_left">
            <h1 className="about_h1">Rlam</h1>
            <img className="aboutImg " src={require('./img/betti_v4.jpg')} alt="belak_betti_trainer"/>
        </div>
    </MDBContainer>
}

export default About;
